<template>
	<view>
		<u-navbar 
		:is-back="true" 
		title="我的活动" 
		title-color='#fff'
		back-icon-color='#fff' 
		:background="{ background:'#213244'}">
		</u-navbar>
		<view  v-for="(item,index) in hd" :key="index">
			<image mode='aspectFill' :src='item.logo' :lazy-load='true'>
			<view>
				<span>{{item.title}}</span>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				hd:[],
			}
		},
		onLoad(uid) {
			this.init(uid);
		},
		methods: {
			init(uid){
				this.AkxhHdlists(uid);
			},
			AkxhHdlists(uid){
				AkxhHd('/catalyzer/AkxhHdjl/page','GET',{'uid':uid}).then(res => {
					if (res.data.code == 200) {
						let result = res.data.result.records;
						let jl = [];
						result.forEach((item,index,array)=>{
							AkxhHd('/catalyzer/AkxhHd/query-by-id','GET',{'id':item.hdId}).then(res => {
								if (res.data.code == 200) {
									let result2 = res.data.result.records;
									item.logo = this.imgURL + res.data.result.logo;
									jl.push(item);
									this.hd = jl;
								}
							});
						});
					}
				})
			}
		}
	}
</script>

